import { Power2, TimelineLite } from 'gsap';

export default function light(Dom_light, Dom_title) {
  var tl = new TimelineLite();
  var vw = window.innerWidth,
    vh = window.innerHeight;

  tl
    .to(Dom_light, 1, {
      attr: {
        r: vw * 0.35,
        cy: vh * 0.4
      },
      ease: Power2.easeOut
    })
    .to(Dom_light, 1, {
      attr: {
        cy: vh * 0.8,
        r: vw * 0.3
      },
      delay: 1
    })
    .to(Dom_light, 1, {
      transformOrigin: "center center",
      skewX: 40,
      rotation: -25,
      attr: {
        r: vw * 0.3
      }
    })
    .to(Dom_title, 1, {
      opacity: 1,
      ease: Power2.easeOut
    })
}



